import { mapActions } from 'vuex';
import Avatar from '@/components/Components/Avatar/index.vue';

export default {
  name: 'RoomListItem',
  props: ['room'],
  data() {
    return {};
  },
  components: {
    Avatar
  },
  methods: {
    roomClick() {
      if (this.room.id !== this.$store.state.roomActive.id) {
        this.setRoomActive(this.room.id);
      }
    },
    ...mapActions(['setRoomActive'])
  }
};



